<template>
  <v-layout
    text-xs-center
    wrap
    v-touch="{
      left: () => swipe('Left'),
      right: () => swipe('Right'),
      up: () => swipe('Up'),
      down: () => swipe('Down')
    }"
  >
    <v-flex xs12>
      <!-- <amplify-s3-image :imagePath="imagePath" /> -->
      <h1 class="display-2 font-weight-bold mb-3">
        {{ $t("message.hello") }} Guide
      </h1>
      <h2>width: {{ width }}</h2>
      <h2>height: {{ height }}</h2>
    </v-flex>
    <v-flex xs12>
      {{ swipeDirection }}
    </v-flex>
  </v-layout>
</template>

<script>
// import { Auth, Storage, Logger } from 'aws-amplify'

export default {
  name: 'Guide',
  data: () => ({
    swipeDirection: 'None'
  }),
  computed: {
    username: {
      get() {
        return this.$store.state.session.username
      }
    },
    width: {
      get() {
        // return document.documentElement.clientWidth
        return this.$window.width
      }
    },
    height: {
      get() {
        // return document.documentElement.clientHeight
        return this.$window.height
      }
    }
  },
  methods: {

    async signOut () {
      this.$store.dispatch('session/logout')
        .catch(e => console.log('logout!!!', e))
    },
    swipe (direction) {
      this.swipeDirection = direction
    }
  },
}
</script>

